.homepage h1 {
  padding: 70px 0;
  text-align: center;
  font-size: 70px;
  color: #34495E;
  -webkit-animation: netflix_style 3.5s;
          animation: netflix_style 3.5s;
  outline: none;
  white-space: nowrap;
}


@-webkit-keyframes netflix_style {
  0% {
    text-shadow: makelongshadow(100,1);
    color:#f3f3f3;
    transform: scale(1.5, 1.5);
  }
  10% {
    text-shadow: makelongshadow(100,1.5);
    color:#f3f3f3;
    transform: scale(1.5, 1.5);
  }
  15% {
     color:#f3f3f3;
  }
  20% {    
    color: #34495E;
    text-shadow: none;
    transform: scale(1.1, 1.1);
  }
  75% {
    opacity: 1;
  }
  80% {
    opacity: 0;    
    color: #34495E;
    transform: scale(0.85, 0.9);
  }
  
  100% {
    opacity: 0;
  }
}


@keyframes netflix_style {
  0% {
    text-shadow: makelongshadow(100,1);
    color:#f3f3f3;
    transform: scale(1.5, 1.5);
  }
  10% {
    text-shadow: makelongshadow(100,1.5);
    color:#f3f3f3;
    transform: scale(1.5, 1.5);
  }
  15% {
     color:#f3f3f3;
  }
  20% {    
    color: #34495E;
    text-shadow: none;
    transform: scale(1.1, 1.1);
  }
  75% {
    opacity: 1;
  }
  80% {
    opacity: 0;    
    color: #34495E;
    transform: scale(0.85, 0.9);
  }
  
  100% {
    opacity: 0;
  }
}

