/* Button styles starts here*/
:root {
  --primary: #4628ff;
  --warning: #ffd028;
  --danger: #eb3f27;
  --success: #75fa83;
  --white: #fdfdfd;
  --dark: #181717;
}

.btn {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
  outline: none;
}

.btn:hover {
  transform: translateY(-3px);
}

.btn--primary--solid {
  background-color: #4628ff;
  background-color: var(--primary);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--warning--solid {
  background-color: #ffd028;
  background-color: var(--warning);
  color: #181717;
  color: var(--dark);
  border: none;
}

.btn--danger--solid {
  background-color: #eb3f27;
  background-color: var(--danger);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--success--solid {
  background-color: #75fa83;
  background-color: var(--success);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--primary--outline {
  background-color: transparent;
  border: 2px solid #4628ff;
  border: 2px solid var(--primary);
  color: #4628ff;
  color: var(--primary)
}


.btn--warning--outline {
  background-color: transparent;
  border: 2px solid #ffd028;
  border: 2px solid var(--warning);
  color: #ffd028;
  color: var(--warning)
}


.btn--danger--outline {
  background-color: transparent;
  border: 2px solid #eb3f27;
  border: 2px solid var(--danger);
  color: #eb3f27;
  color: var(--danger)
}


.btn--success--outline {
  background-color: transparent;
  border: 2px solid #75fa83;
  border: 2px solid var(--success);
  color: #75fa83;
  color: var(--success)
}


.btn--medium {
  padding: 10px 20px;
  font-size: 18px;
}


.btn--large {
  padding: 15px 30px;
  font-size: 30px;
}

.btn--small {
  padding: 5px 10px;
  font-size: 14px;
}

.homepage-button {
  text-align: center;
  margin: 20px;
  padding: 20px;
}

