/*Comment box CSS Starts here*/
.commentbox-container
{
    width: 600px;
    padding: 15px 10px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    background-color:#f9f2f9;
}

.actualcomment {
  margin: 20px;
}
.comment-container
{
    width: 500px;
    border: 2px solid #333;
    border-radius: 20px;
    padding: 15px 10px;
    margin-left: auto;
    margin-right: auto;
    
}

.comment-container h2
{
    text-align: center;
    margin-bottom: 15px
}
.comment-container textarea
{
    height: 20px;
    width: 100%;
    border: none;
    border-bottom: 2px solid #aaa;
    background-color: transparent;
    margin-bottom: 10px;
    resize: none;
    outline: none;
    transition: .5s
}

.comment-container input[type="submit"], button
{
    padding: 10px 15px;
    border: none;
    outline: none;
    border-radius: 5px;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
}
.comment-container input[type="submit"]
{
    color: #fff;
    background-color: #273c75
}

.comment-container button
{
    color: #333;
    background-color: transparent
}

